<template>
  <div class="container">
    <!--  -->
    <van-nav-bar
      fixed
      left-arrow
      @click-left="$router.back()"
      title=""
    ></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div v-for="(obj, index) in list" :key="index">
        <!--  -->
        <div class="chat-item left" v-if="obj.name === 'xs'">
          <van-image
            fit="cover"
            round
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
          <div class="chat-pao">{{ obj.msg }}</div>
        </div>

        <!--  -->
        <div class="chat-item right" v-if="obj.name === 'me'">
          <div class="chat-pao">{{ obj.msg }}</div>
          <van-image
            fit="cover"
            round
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
        </div>
      </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field v-model="word" placeholder="...">
        <template #button>
          <span style="font-size: 12px; color: #999" @click="sendFn"></span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
//  socket.io-client 
import { io } from "socket.io-client";
import { getToken } from "@/utils/user_token/index";

export default {
  name: "Chat",
  data() {
    return {
      word: "", // 
      // 
      list: [
        //  name 
        { name: "xs", msg: "hi" },
        { name: "me", msg: "" },
      ],
      socket: null,
    };
  },
  created() {
    //  websocket 
    this.socket = io("http://toutiao.itheima.net", {
      query: {
        token: getToken(),
      },
      transports: ["websocket"],
    });

    // 
    this.socket.on("connect", () => {
      console.log("");
    });

    // 
    this.socket.on("message", (data) => {
      //  list 
      this.list.push({
        name: "xs",
        msg: data.msg,
      });

      this.$nextTick(() => {
        this.scrollToBottom();
      });
    });
    
  },
  //  sock 
  beforeDestroy() {
    // 
    this.socket.close();

    //  websocket 
    this.socket = null;
  },
  methods: {
    sendFn() {
      // 
      if (!this.word) return;

      //  list 
      this.list.push({
        name: "me",
        msg: this.word,
      });
      //  websocket 
      this.socket.emit("message", {
        msg: this.word,
        timestamp: new Date().getTime(),
      });

      this.$nextTick(() => {
        this.scrollToBottom();
      });

      // 
      this.word = "";
    },

    // 
    scrollToBottom() {
      //  Item 
      const chatItem = document.querySelectorAll(".chat-item");
      //  DOM 
      const lastItem = chatItem[chatItem.length - 1];
      //  scrollIntoView() 
      lastItem.scrollIntoView({
        behavior: "smooth", // 
      });
    },
  },
};
</script>

